import React from "react";
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';

const Note = (props) =>{

    const deleteNote = () =>{
        props.deleteItem(props.id);
    }

    return (<>
        <div className="note_container">
        <div className="note">
            <h2>{props.title}</h2>
            <br/>
            <p>{props.content}</p>
            <DeleteOutlineIcon className ='deleteIcon' onClick = {deleteNote} />
        </div>
        </div>
    </>);
}

export default Note;